.users-list {
  display: flex;
  flex-direction: column;
  gap: 20px;
}

.user {
  display: block;
  box-shadow: inset 0 0 10px rgb(255, 0, 0);
  background-color: rgb(255, 237, 205);
  padding: 20px;
  border-radius: 5px;

  &--todos-is-completed {
    box-shadow: inset 0 0 10px rgb(76, 169, 0);
    background-color: rgb(250, 255, 235);
  }
}