* {
  box-sizing: border-box;
  padding: 0;
  margin: 0;
  list-style: none;
}

.App {
  background: linear-gradient(145deg , rgb(254, 233, 228), rgb(213, 224, 142));
  box-shadow: 10px 10px 20px black;
  border: 1px solid #000;
  border-radius: 10px;
  display: flex;
  gap: 30px;
  flex-direction: column;
  margin: 50px auto;
  padding: 30px;
  width: 850px;
  min-height: 600px;
}

.header{
  border-bottom: 1px solid #000;
  text-align: center;
  font-size: 30px;

}


.main {
  display: flex;
  flex-direction: column;
  flex-grow: 1;
  gap: 20px;
}

.form {
  display: flex;
  height: 100px;
  gap: 20px;

}

.wraper {
display: flex;
flex-direction: column;
flex-grow: 1;
gap: 10px;

}

.select {
  flex-grow: 1;
}

.button-add {
flex-grow: 1;
}

.text-field {
resize: none;
flex-grow: 2;

}

.board {
  box-shadow: inset 0 0 25px rgb(135, 132, 132);
  background-color: rgb(250, 247, 228);
  padding: 30px;
  flex-grow: 1;
}
