  &__info {
    display: flex;
  }

  &__about {
    background-color: rgb(250, 240, 179);
    color: rgb(0, 0, 0);
    flex-grow: 1;
    display: flex;
    flex-direction: column;
    box-shadow: inset 0 0 5px#000;
    border-radius: 5px;

    padding: 5px 0 5px 20px;
    gap: 5px;
  }

  &__todos {
    margin-top: 20px;
    padding: 20px;
    border-top: 1px solid black;
  }
}

.label {
  font-weight: 700;
  margin-right: 10px;
  font-style: normal;
}

.title {
  margin: auto;
  text-decoration: underline;
  text-align: center;
  font-weight: 700;
  font-size: 25px;
  width: 300px;
}

.phone {
  font-style: italic;
}